// =============================================================================
// GOVERNANCE TYPES
// =============================================================================
// Type definitions for governance configuration and proposal management.
// Enables controlled parameter changes with timelock and audit trail.

import type { LoadThresholds } from './load-management';

// =============================================================================
// PROPOSAL TYPES
// =============================================================================

/**
 * Status of a governance proposal.
 */
export type ProposalStatus = 'draft' | 'pending' | 'applied' | 'rejected';

/**
 * A governance proposal for changing a configuration parameter.
 */
export interface GovernanceProposal {
  id: string;
  key: string;
  old_value: unknown;
  new_value: unknown;
  status: ProposalStatus;
  created_by: string;
  created_at: Date;
  ready_at: Date;
  applied_at: Date | null;
  rejection_reason?: string;
}

/**
 * Parameters for creating a new proposal.
 */
export interface CreateProposalParams {
  key: string;
  new_value: unknown;
  created_by: string;
  timelock_hours?: number; // Default: 48 hours
}

/**
 * Result of applying proposals.
 */
export interface ApplyProposalsResult {
  applied: GovernanceProposal[];
  failed: Array<{ proposal: GovernanceProposal; error: string }>;
}

// =============================================================================
// CONFIGURATION TYPES
// =============================================================================

/**
 * Anomaly detection thresholds.
 */
export interface AnomalyThresholds {
  max_error_rate: number;        // 0-1 (e.g., 0.1 = 10%)
  max_latency_ms: number;        // e.g., 5000
  min_success_rate: number;      // 0-1 (e.g., 0.95 = 95%)
  evaluation_window_minutes: number;
}

/**
 * Fee configuration.
 */
export interface FeeConfig {
  platform_fee_percentage: number;  // Basis points (e.g., 25 = 0.25%)
  min_fee_wei: string;              // Minimum fee in wei
  max_fee_percentage: number;       // Maximum fee in basis points
}

/**
 * Circuit breaker configuration.
 */
export interface CircuitBreakerConfig {
  failure_threshold: number;        // Number of failures to open
  success_threshold: number;        // Successes to close
  half_open_timeout_ms: number;     // Time before half-open
  open_timeout_ms: number;          // Time before recovery attempt
}

/**
 * Complete governance configuration.
 */
export interface GovernanceConfig {
  // Fee settings
  platform_fee_percentage: number;
  min_fee_wei: string;
  max_fee_percentage: number;

  // Anomaly detection
  anomaly_thresholds: AnomalyThresholds;

  // Load management
  load_thresholds: LoadThresholds;

  // Circuit breaker
  circuit_breaker: CircuitBreakerConfig;

  // Batch settlement
  batch_max_size: number;
  batch_min_size: number;
  batch_timeout_minutes: number;

  // Canary / Registration
  default_canary_share: number;     // 0-1 (e.g., 0.01 = 1%)
  max_canary_share: number;         // 0-1 (e.g., 0.20 = 20%)
  promotion_success_threshold: number; // 0-1
}

/**
 * A single configuration entry in the database.
 */
export interface ConfigEntry {
  key: string;
  value: unknown;
  updated_at: Date;
  updated_by: string;
}

// =============================================================================
// DEFAULTS
// =============================================================================

/**
 * Default anomaly thresholds.
 */
export const DEFAULT_ANOMALY_THRESHOLDS: AnomalyThresholds = {
  max_error_rate: 0.1,              // 10%
  max_latency_ms: 5000,             // 5 seconds
  min_success_rate: 0.95,           // 95%
  evaluation_window_minutes: 60,    // 1 hour
};

/**
 * Default circuit breaker configuration.
 */
export const DEFAULT_CIRCUIT_BREAKER_CONFIG: CircuitBreakerConfig = {
  failure_threshold: 5,
  success_threshold: 3,
  half_open_timeout_ms: 30000,      // 30 seconds
  open_timeout_ms: 60000,           // 1 minute
};

/**
 * Default governance configuration.
 */
export const DEFAULT_GOVERNANCE_CONFIG: GovernanceConfig = {
  // Fee settings
  platform_fee_percentage: 25,      // 0.25% (25 basis points)
  min_fee_wei: '1000000000000',     // 0.000001 ETH
  max_fee_percentage: 500,          // 5% max

  // Anomaly detection
  anomaly_thresholds: DEFAULT_ANOMALY_THRESHOLDS,

  // Load management (matching load-management.ts defaults)
  load_thresholds: {
    high_pending_batches: 250,
    critical_pending_batches: 500,
    high_pending_payments: 25000,
    critical_pending_payments: 50000,
    high_in_flight: 10,
    critical_in_flight: 25,
  },

  // Circuit breaker
  circuit_breaker: DEFAULT_CIRCUIT_BREAKER_CONFIG,

  // Batch settlement
  batch_max_size: 100,
  batch_min_size: 1,
  batch_timeout_minutes: 30,

  // Canary / Registration
  default_canary_share: 0.01,       // 1%
  max_canary_share: 0.20,           // 20%
  promotion_success_threshold: 0.95, // 95%
};

/**
 * Default timelock for proposals (48 hours).
 */
export const DEFAULT_TIMELOCK_HOURS = 48;

// =============================================================================
// VALIDATION
// =============================================================================

/**
 * Valid configuration keys (for validation).
 */
export const VALID_CONFIG_KEYS = [
  'platform_fee_percentage',
  'min_fee_wei',
  'max_fee_percentage',
  'anomaly_thresholds',
  'anomaly_thresholds.max_error_rate',
  'anomaly_thresholds.max_latency_ms',
  'anomaly_thresholds.min_success_rate',
  'anomaly_thresholds.evaluation_window_minutes',
  'load_thresholds',
  'load_thresholds.high_pending_batches',
  'load_thresholds.critical_pending_batches',
  'load_thresholds.high_pending_payments',
  'load_thresholds.critical_pending_payments',
  'load_thresholds.high_in_flight',
  'load_thresholds.critical_in_flight',
  'circuit_breaker',
  'circuit_breaker.failure_threshold',
  'circuit_breaker.success_threshold',
  'circuit_breaker.half_open_timeout_ms',
  'circuit_breaker.open_timeout_ms',
  'batch_max_size',
  'batch_min_size',
  'batch_timeout_minutes',
  'default_canary_share',
  'max_canary_share',
  'promotion_success_threshold',
] as const;

export type ConfigKey = typeof VALID_CONFIG_KEYS[number];

/**
 * Check if a key is a valid configuration key.
 */
export function isValidConfigKey(key: string): key is ConfigKey {
  return VALID_CONFIG_KEYS.includes(key as ConfigKey);
}

// =============================================================================
// API TYPES
// =============================================================================

/**
 * Request to create a governance proposal.
 */
export interface CreateProposalRequest {
  key: string;
  new_value: unknown;
  timelock_hours?: number;
}

/**
 * Response for proposal creation.
 */
export interface CreateProposalResponse {
  proposal: GovernanceProposal;
  message: string;
}

/**
 * Response for listing proposals.
 */
export interface ListProposalsResponse {
  proposals: GovernanceProposal[];
  total: number;
}

/**
 * Response for applying proposals.
 */
export interface ApplyProposalsResponse {
  applied_count: number;
  applied: GovernanceProposal[];
  failed_count: number;
  failed: Array<{ id: string; error: string }>;
}

/**
 * Response for getting current config.
 */
export interface GetConfigResponse {
  config: GovernanceConfig;
  last_updated_at: Date;
}


